/* calendar */
.za-calendar .za-calendar-action::after {
  content: '';
  display: table;
  clear: both; }

.za-calendar {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3000;
  width: 100%;
  min-height: 100%;
  padding-bottom: 20px;
  padding-top: 50px;
  background: #fff; }
  .za-calendar ul,
  .za-calendar li {
    list-style: none;
    padding: 0;
    margin: 0; }
  .za-calendar .za-calendar-item {
    text-align: center; }
  .za-calendar .za-calendar-action {
    background-color: #f3f2f7;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
    cursor: pointer; }
    .za-calendar .za-calendar-action .ok-btn {
      padding: 15px;
      float: left;
      color: #12c287; }
    .za-calendar .za-calendar-action .cancel-btn {
      padding: 15px;
      float: right; }
  .za-calendar .za-calendar-month {
    padding: 15px 0;
    color: #000;
    font-size: 15px;
    font-weight: bold;
    border-bottom: 1px solid #e9e9e9; }
  .za-calendar .za-calendar-day:after {
    content: '';
    display: block;
    height: 0;
    overflow: hidden;
    clear: both; }
  .za-calendar .za-calendar-day li {
    float: left;
    width: 14.285714285714286%;
    text-align: center;
    vertical-align: middle;
    line-height: 45px; }
  .za-calendar .za-calendar-date {
    width: 100%;
    font-size: 15px; }
    .za-calendar .za-calendar-date:after {
      content: '';
      display: block;
      height: 0;
      overflow: hidden;
      clear: both; }
  .za-calendar .za-calendar-selected-suffix {
    display: block; }
  .za-calendar .za-calendar-date li {
    position: relative;
    float: left;
    width: 14.285714285714286%;
    height: 45px;
    line-height: 45px;
    text-align: center;
    overflow: hidden;
    word-break: break-all;
    cursor: pointer;
    color: #666;
    vertical-align: middle; }
    .za-calendar .za-calendar-date li .date-item {
      display: block;
      width: 45px;
      height: 45px;
      line-height: 45px;
      margin: 0 auto;
      border-radius: 45px; }
  .za-calendar .za-calendar-date li.za-calendar-festival {
    font-weight: bold; }
  .za-calendar .za-calendar-date li.za-calendar-selected .date-item {
    position: relative;
    z-index: 10;
    background: #12c287;
    color: #fff;
    font-weight: bold; }
  .za-calendar .za-calendar-date li.za-calendar-selected.za-calendar-range-selected.za-calendar-start-selected {
    border-top-left-radius: 45px;
    border-bottom-left-radius: 45px; }
  .za-calendar .za-calendar-date li.za-calendar-selected.za-calendar-range-selected.za-calendar-end-selected {
    border-top-right-radius: 45px;
    border-bottom-right-radius: 45px; }
  .za-calendar .za-calendar-date li.za-calendar-selected.za-calendar-start-selected:after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    top: 0;
    height: 100%;
    left: 4px;
    background-color: #ccf2e6;
    border-top-left-radius: 45px;
    border-bottom-left-radius: 45px; }
  .za-calendar .za-calendar-date li.za-calendar-selected.za-calendar-end-selected:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    right: 4px;
    background-color: #ccf2e6;
    border-top-right-radius: 45px;
    border-bottom-right-radius: 45px; }
  .za-calendar .za-calendar-date li.za-calendar-range-selected {
    background: #ccf2e6;
    font-weight: bold; }
  .za-calendar .za-calendar-date li.za-calendar-invalid {
    color: #c9c9c9;
    cursor: auto; }
  .za-calendar .za-calendar-date li.za-calendar-invalid:hover {
    background: none; }
  .za-calendar .za-calendar-more {
    text-align: center;
    padding: 15px 0; }
  .za-calendar .za-calendar-mask {
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 2999;
    width: 100%;
    height: 100%;
    background-color: #fff; }
  .za-calendar .za-calendar-year {
    display: block;
    padding: 15px;
    text-align: left;
    color: #666;
    font-weight: 800;
    border-bottom: 1px solid #ccf2e6;
    background-color: #ccf2e6; }
